.insta-buy-button:hover {
    animation-name: hover-buy-button; 
    animation-duration: 1s;
    background-color: white;
    font-size: 14pt;

    span {
        color: black;
    }
}

.insta-buy-button:active {
    animation-name: press-buy-button; 
    animation-duration: 0.3s;
    background-color: #9effa1;
    font-size: 12pt;

    span {
        color: black;
    }
}

.insta-buy-button {
    background-color: black;
    color: white;
    padding: 8px;
    border-radius: 15px;
    bottom: 50px;
    font-size: 12pt;
    left: 50%;
    right: 50%;
    transform: translateX(-50%);
    width: 80px;
    font-weight: 600;
    text-align: center;
    position: absolute;
    cursor: pointer;

    span {
        background: -webkit-linear-gradient(60deg, red 0%, #ffe500 40%, #00f7ff 60%, blue 80%, #ff00a1 90%);
        background: -moz-linear-gradient(60deg, red 0%, #ffe500 40%, #00f7ff 60%, blue 80%, #ff00a1 90%);
        background: linear-gradient(60deg, red 0%, #ffe500 40%, #00f7ff 60%, blue 80%, #ff00a1 90%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        display: inline-block;
        -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
        background-repeat: no-repeat;
        padding-left: 15px;
        padding-right: 15px;
        animation: shimmer 2.7s infinite;
    }
}


@media screen and (max-width: 600px) {
    .spores-playlist-button.opened {
        border-bottom: 1px solid white;
    }
    .spores-playlist-button {
        border-bottom: 1px solid transparent;
        width: 45px;
        height: 45px;
        position: absolute;
        left: 20px;
        bottom: 20px;
    }
    
    .insta-buy-button {
        font-size: 10pt;
        right: 15px;
        left: unset;
        top: 20px;
        height: 35px;
        width: 70px;
        transform: unset;
    }
}

@keyframes shimmer {
    100% {-webkit-mask-position:left}
}

@keyframes hover-buy-button {
    from {
        background-color: transparent;
        font-size: 12pt;
    }

    to {
        background-color: white;
        font-size: 14pt;
    }
}

@keyframes press-buy-button {
    from {
        background-color: white;
        font-size: 14pt;
    }

    to {
        background-color: #9effa1;
        font-size: 12pt;
    }
}



