@import url("https://fonts.googleapis.com/css2?family=Spline+Sans:wght@400;600;700&display=swap");
@import url('https://rsms.me/inter/inter.css');

:root {
  --color-title: rgba(45, 58, 148, 0.6);
  --color-cta-bg: rgba(45, 58, 148, 0.6);
  --color-cta-text: #ebbbb1;
  --color-highlight: #000000;
  --color-text-main: rgba(45, 58, 148, 0.6);
}

html,
body,
#root {
  margin: 0;
  height: 100%;
  background-color: white;
}

body {
  /* font-family: stem; */
  font-family: 'Inter', sans-serif !important;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  user-select: none;
}
