div[aria-labelledby="rk_connect_title"],  div[aria-labelledby="rk_account_modal_title"]  {
    top: 0px !important;
}

.buy-now-view {
    color: black;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;

    .buy-spore-players-text {
        color: black;
        text-align: center;
        padding-top: 100px;
        font-size: 32pt;
        background: linear-gradient(60deg, rgba(255,0,0,1) 0%, rgba(255,229,0,1) 25%, rgba(0,247,255,1) 50%, rgba(0,0,255,1) 75%, rgba(255,0,161,1) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: inline-block;
    -webkit-mask: linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
    background-repeat: no-repeat;
    animation: Landing_shimmer__3BITt 2.5s infinite;
    font-weight: 600;
    font-size: 50px;
    }

    .buy-grid-header.with-cart {
        height: 160px;
        animation-name: expand-header;
        animation-duration: 1s;
        overflow: hidden;

        .cart {
            height: 240px;
            display: flex;
            flex-direction: column;
            animation: fade-in-cart;
            animation-duration: 1s;

            align-items: flex-start;
        }

        img.spores-logo {
            margin-top: 35px;
            animation-name: push-logo-down;
            animation-duration: 0.8s;
            width: 35px;
            height: 35px;
        }

        .buy-button {
            margin-top: 8px;
            align-items: flex-start;
            background-color: black;
            border-radius: 8px;
            padding: 8px;
            color: white;
        }
    }

    .buy-grid-header {
        backdrop-filter: blur(16px);
        position: fixed;
        top: 0px;
        left: 0px;
        height: 65px;
        width: 100%;
        background-color: #ffffffa8;
        z-index: 2;
        display: flex;
        border-bottom: 2px solid #a7a7a729;

        .buy-page-title {
            margin-top: 17px;
            font-weight: 550;
            font-size: 18px;
            margin-left: 0px;
        }

        .cart {
            margin: 15px;
            max-width: 80%;

            .buy-info-container {
                display: flex;
                min-width: 500px;
                align-items: flex-start;

                .clear-cart:hover {
                    animation-name: hover-clear-button; 
                    animation-duration: 0.8s;
                    background-color: white;
                }
                .clear-cart {
                    background-color: transparent;
                    cursor: pointer;
                    margin-left: auto;
                    margin-top: 9px;
                    padding: 3px;
                    padding-left: 9px;
                    padding-right: 9px;
                    border-radius: 18px;
                    border: 2px solid black;
                    font-size: 9pt;
                    line-height: 28px;
                    align-items: flex-start;
                }

                .price-info {
                    margin-left: 20px;
                    margin-top: 18px;
                    color: #6c6969;

                    .total-price {
                        font-weight: 600;
                        color: black;
                    }
                }
            }

            .cart-options {
                max-width: 99%;
                overflow-x: scroll;
                display: flex;
            }
            .cart-option:hover {
                position: relative;
                .tag-remove {
                    position: absolute;
                    display: block;
                }
            }
            .cart-option {
                cursor: pointer;
                .tag-remove {
                    display: none;
                    position: absolute;
                    z-index: 100;
                    top: 0px;
                    right: 8px;
                    color: red;
                    font-weight: 300;
                    font-family: Arial, sans-serif;
                }
                animation-name: fade-in-cart;
                animation-duration: 1s;
                margin-right: 4px;
                border-radius: 2px;
                overflow: hidden;
                height: 80px;
                display: flex;

                .song-info {
                    margin-left: 20px;
                    font-size: 9pt;

                    .artist {
                        color: #877676;
                    }
                }
                
                img {
                    height: 100%;
                    width: 80px;
                    min-width: 80px;
                    image-cover: fit;
                }
            }
        }
        .logo-container {
            width: 70px;
        }
        .spores-logo {
            width: 20px;
            height: 20px;
            margin-top: 21px;
            margin-left: 12px;
        }
        
        .connect-button-wrapper {
            margin-top: 15px;
            transform: scale(0.8);
            transform-origin: top left;
            margin-left: auto;
        }
    }

    .buy-grid {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        padding-top: 160px;

        .token-sale-card:hover {
            img.token-preview {
                animation: brighten-card;
                animation-duration: 0.5s;
                filter: brightness(1.5);
            }
        }

        .token-sale-card.in-cart {
            border-color: #44ff44;
            position: relative;

            .check {
                top: 8px;
                right: 8px;
                position: absolute;
                display: inline-block;
                transform: rotate(327deg);
                height: 10px;
                width: 20px;
                border-bottom: 4px solid #44ff44;
                border-left: 4px solid #24ff24;
            }
        }

        .token-sale-card.has-error {
            z-index: 10000;
        }
        .token-sale-card {
            position: relative;
            z-index: 1;
            cursor: pointer;
            margin: 15px;
            width: 21%;
            border: 2px solid #a7a7a729;
            //background-color: #f1f7df;
            background: linear-gradient(to right, #ededf4b0, #fafafaa1), url(https://grainy-gradients.vercel.app/noise.svg) !important;
            border-radius: 10px;
            //overflow: hidden;
            font-weight: 500;
            font-size: 80%;

            .already-sold {
                backdrop-filter: blur(8px);
                position: absolute;
                bottom: -100px;
                width: 200px;
                background-color: #eaeaeae0;
                left: 30px;
                color: black;
                padding: 10px;
                font-size: 9pt;
                opacity: 0;
                animation-name: fade-away;
                animation-duration: 12s;
            }
            
            img.token-preview {
                filter: brightness(1.1);
                width: 100%;
                height: 86%;
                border-top-left-radius: 10px;
                border-top-right-radius: 10px;
            }

            .card-info {
                display: flex;
                padding: 4%;

                .ask-price.not-for-sale {
                    color: #817c7c99;
                }
                
                .ask-price {
                    margin-left: auto;
                }
            }
        }
    }

    .buy-now-button {
        background-color: black;
        color: white;
        padding: 8px;
    }
}

@keyframes expand-header {
    from {
        height: 65px;
    }

    to {
        height: 160px;
    }
}

@keyframes push-logo-down {
    from {
        margin-top: 12px;
        width: 20px;
        height: 20px;
    }

    to {
        margin-top: 35px;
        width: 35px;
        height: 35px;
    }
}

@keyframes fade-in-cart {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}



@keyframes brighten-card {
    from {
        filter: brightness(1.1);
    }

    to {
        filter: brightness(1.5);
    }
}

@keyframes hover-clear-button {
    from {
        background-color: transparent;
    }

    to {
        background-color: white;
    }
}



@media screen and (max-width: 600px) {

    .buy-now-view {
        .buy-grid-header {
            .cart {
                max-width: 100px !important;
            }
            .clear-cart {
                position: absolute;
                top: 50px;
                right: 20px;
                z-index: 100000;
            }
        }
        .buy-grid {
            flex-wrap: unset !important;
            flex-direction: column !important;

            .token-sale-card {
                width: 90%;
            }
       }
    }
}

@keyframes fade-away {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}
