.finalize-modal-drop {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 10000;
    display: flex;
    background-color: #4e4a4abd;

    .shrooms-video.confirming {
        opacity: 0.2;
    }
    .shrooms-video {
        position: absolute;
        z-index: 1;
        top: 0px;
        left: 0px;
        min-width: 100%;
        min-height: 100%;
        width: auto;
        height: auto;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }

    .pulse-container {
        margin-left: auto;
        margin-right: auto;
        margin-top: 25px;
    }

    .open-your-player {
        margin-top: auto;
        background-color: white;
        color: black;
        padding: 8px;
        border-radius: 8px;
        cursor: pointer;
        text-align: center;
    }

    .finalize-modal {
        box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        margin: auto;
        width: 400px;
        height: 500px;
        color: white;
        background-color: #00000070;
        border-radius: 12px;
        padding: 30px;
        border: 2px solid black;
        backdrop-filter: blur(16px);

        .close-button {
            position: absolute;
            top: 8px;
            right: 18px;
            font-size: 16pt;
            cursor: pointer;
        }

        .logo-container {
            display: flex;
            margin-bottom: 20px;

            .spores-logo-black {
                width: 35px;
                height: 35px;
                margin-left: auto;
                margin-right: auto;
            }
        }

        .sp-modal-title {
            display: flex;

            .purchase-part {
                margin-left: auto;
                font-size: 9pt;
                line-height: 30px;
                color: gray;
                .total-parts, .part-number {
                    color: white;
                }
            }

            .sp-title {
                font-size: 18pt;
                font-weight: 600;
            }
        }

        .sp-modal-body {
            margin-top: 20px;
            display: flex;
            flex-direction: column;
            flex: 1;
        }

        .connect-wallet-container {
            margin-top: auto;
            margin-left: auto;
            margin-right: auto;
        }
        .complete-buy:hover {
            animation-name: brighten-card;
            animation-duration: 1s;
            filter: brightness(1.5);
        }


        .low-balance:hover {
            animation-name: unset !important;
            cursor: not-allowed !important;
            filter: unset !important;
        }

        .complete-buy.low-balance {
            background-color: gray;

        }

        .complete-buy {
            cursor: pointer;
            background-color: #00ff00;
            background-image: linear-gradient(45deg, #b3c3ff, transparent);
            color: black;
        }

        .waiting-for-wallet {
            display: flex;
            background-color: black;
            color: white;

            .inner {
                display: flex;
                margin: auto;

                span.helper-text {
                    margin-right: 10px;
                }

            }
        }

        .error-text {
            margin-top: 8px;
            background-color: #dadada2e;
            color: white;
            padding: 8px;
            font-size: 10pt;
            display: flex;

            img {
                margin-top: 8px;
                width: 25px;
                height: 25px;
                margin-right: 15px;
            }
        }

        .complete-buy, .waiting-for-wallet {
            margin-top: auto;
            width: 100%;
            font-size: 12pt;
            text-align: center;
            padding: 30px;
        }

        .thumbnail-and-price {
            margin-top: 20px;
            display: flex;

            .token-price {
                margin-left: auto;
                .price-label {
                    text-align: right;
                    font-size: 8pt;
                    color: gray;
                }

                .price-eth {
                    font-weight: 600;
                    font-size: 24pt;
                    color: white;
                }
            }
            img {
                width: 80px;
                height: 80px;
                border-radius: 8px;
                filter: brightness(1.2);
            }
        }
    }
}
