section {
display: flex !important;
flex-direction: column;
justify-content: center;
margin-left: 15px;
margin-right: 15px;
}

header {
margin-top: 50px;
margin-bottom: 200px;
position: relative;
}

header h1 {
font-weight: 600;
font-size: 54px;
margin: 11px 0;
color: #000000;
}

header sup {
display: inline-block;
border-radius: 4px;
font-size: 14px;
line-height: 151.5%;
padding: 1px 9px;
position: relative;
top: -48px;
left: -25px;
}

nav {
font-size: 32px;
line-height: 38px;
width: 150px;
}

nav a {
display: block;
color: black;
}

a {
text-decoration: none;
color: black;
}

a:hover {
text-decoration: underline;
color: black;
}

@media only screen and (max-width: 768px) {
