.spore {
    .player-helper {

        svg.visual-links-spore {
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: 10000;
            pointer-events: none;
            polygon {
                fill: url(#AnnotationGradient);
            }
        }

        .helper-box {
            position: absolute;
            left: 30px;
            top: 80px;
            width: 300px;
            height: 340px;
            border-radius: 10px;
            padding: 15px;
            border: 2px solid white;
            background-color: black;

            p.selected {
                background-color: white;
                color: black;
            }
        }

        display: flex;
        //backdrop-filter: blur(8px);
        background-color: #0000006b;
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        z-index: 100;

        .mock-player {
            position: relative;
            width: 340px;
            height: 400px;
            margin: auto;
            left: 0px;
            right: 0px;
            top: 30px;
            bottom: 0px;
            border-radius: 30px;
            border: 2px solid white;
            background-image: radial-gradient(#000000 8%, transparent);

            .center-button:hover, .sp-button:hover, .led-button:hover, .sp-slider:hover,
            .center-button.selected, .sp-button.selected, .led-button.seleected, .sp-slider.selected,
            {
                background-color: #a4aed863;
                cursor: pointer;
            }

            .center-button {
                position: absolute;
                width: 45px;
                height: 45px;
                border-radius: 60px;
                border: 2px solid white;
                top: 202px;
                left: 147px
            }

            .sp-button {
                border: 2px solid white;
                border-radius: 18px;
                position: absolute;
                width: 56px;
                height: 56px;
            }

            .sp-button.button2 {
                left: 60px;
                bottom: 20px;
            }

            .sp-button.button3 {
                right: 60px;
                bottom: 20px;
            }

            .led-button.sp-button {
                width: 112px;
                top: 15px;
                left: 115px;
            }

            .sp-slider {
                position: absolute;
                border-radius: 60px;
                border: 2px solid white;
                display: flex;
                z-index: 1000000;

                span {
                    margin: auto;
                    color: white;
                    font-size: 16pt;
                }
            }

            .slider1 {
                left: 20px;
                top: 122px;
            }

            .slider2 {
                left: 176px;
                top: 270px;
            }
            
            .slider3 {
                left: 64px;
                top: 228px;
            }

            .slider4 {
                left: 218px;
                top: 80px;
            }

            .slider3, .slider4 {
                height: 142px;
                width: 60px;
            }

            .slider1, .slider2 {
                width: 142px;
                height: 60px;
                
            }
        }
    }
}


@media screen and (max-width: 600px) {
    .spore {
        .player-helper {
            cursor: pointer;
            z-index: 100000;
            background-color: #0000004d;
            .helper-box {
                border-radius: 0px !important;
                top: 4px !important;
                bottom: unset !important;
                height: 140px !important;
                width: 94%;
                left: 3%;
                font-size: 10pt;
                padding: 5px;
            }
            
            .mock-player {
                top: 0px !important;
            }

            .x-button {
                bottom: 0px !important;
                top: unset !important;
                line-height: 39px;
                right: 50% !important;
                left: 50% !important;
                width: 100%;
                background-color: #4bfe4b;
                padding: 15px;
                text-align: center;
                color: black !important;
                font-size: 30pt !important;
                transform: translate(-50%, 0%);
            }
        }

    }
}
