.spores-playlist {
    margin-top: 30px;
    margin-left: -15px;
    margin-right: -15px;
    .song-item:hover {
        background-color: #ffffff2f;
    }

    .song-item.current{  
        color: black;
        background-color: white;
    }
    .artist-name {
        color: #aab7ba;
        font-size: 10pt;
        margin-bottom: 5px;
    }
    .song-item {
        font-size: 9pt;
        display: flex;
        padding: 2px;
        padding-left: 15px;

        .pulse-loader {
            margin-top: 3px;
            margin-left: 10px;
        }
    }

    .see-latest {
        border-top: 1px solid white;
        margin-top: 12px;
        padding-top: 12px;
        padding-bottom: 8px;

        img {
            width: 10px;
            margin-left: 10px;
        }
    }
}

@keyframes fade-in-menu {
    from {
        height: 0px;
        opacity: 0;
    }

    to {
        height: 100px;
        opacity: 1;
    }
}

@media screen and (max-width: 600px) {
    .canvas-container {
        z-index: 1;
    }

    .song-credits {
        display:  none;
    }

    .spores-playlist.hide {
        display: none;
    }

    .caurosel {
        z-index: 10000 !important;
    }

    .spores-playlist {
        z-index: 10000;
        position: fixed;
        bottom: 70px;
        left: 40px;
        padding: 15px;
        cursor: pointer;
        border-radius: 8px;
        background-color: #a0a4ac9e;
        backdrop-filter: blur(8px);

        .artist-name {
            font-size: 13pt;
            color: var(--sporeText);
        }

        .song-item {
            font-size: 12pt;
            padding-right: 15px;
            cursor: pointer;
        }
    }
}
