

.nft-jacker.jacked {
    padding: unset;
}

.nft-jacker {
    background-color: black;
    height: 100%;
    padding: 20%;
    display: flex;
    flex-direction: column;


    h1 {
        text-align: center;
        color: #61fc9f;
    }

    .player-container.show {
        margin: auto;
    }
    .player-container.hide {
        display: none;
    }
    
    .nft-link.hide {
        display: none;
    }
    .nft-link {
        margin-top: 5%;
        margin-left: auto;
        margin-right: auto;
        width: 80%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        input {
            width: 100%;
            color: #61fc9f;
            background-color: #131415;
            border-radius: 8px;
            outline: none !important;
            border: 1px solid #ffffff4a;
            padding: 10px;
        }

        .jack-btn {
            position: relative;
            padding: 12px;
            background-color: white;
            color: black;
            border: 10px;
            border-radius: 15px;
            text-align: center;
            align-items: flex-start;
            font-size: 14pt;
            font-weight: 600;
            width: 220px;
            margin-left: auto;
            margin-right: auto;
            margin-top: 5%;
            cursor: pointer;
        }

        .jack-btn:hover {
            filter: brightness(.9);
        }
        .jack-btn:before {
            position: absolute;
            top: 50%;
            left: 22px;
            width: 24px;
            height: 25px;
            content: "";
            transform: translateY(-50%);
            background-image: url("data:image/svg+xml,%3Csvg width='24' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5 12.5h14M12 5.5l7 7-7 7' stroke='%23000' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
        }
    }
}
