.player-menu {
    position: relative;
    cursor: pointer;
    width: 80px;
    margin-left: 10px;
    border-bottom: 1px solid white;
    text-decoration: none !important;

    .player-menu-items {
        padding-top: 8px;
        position: absolute;
        top: 35px;
        background-color: #bcbcbc66;
        backdrop-filter: blur(8px);
        color: white;
        max-height: 400px;
        overflow-y: scroll;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;

        .go-to-player.selected {
            background-color: white;
            color: black;
        }
        
        .go-to-player {
            display: flex;
            img.menu-thumb {
                display: unset !important;
                margin-left: auto;
                margin-right: 10px;
                width: 30px;
                height: 30px;
                border-radius: 3px;
            }
            padding: 4px;
            width: 150px;
            padding-left: 18px;
            font-size: 12pt;
        }
    }

    .arrow-down, .arrow-up {
        position: absolute;
        top: 6px;
        right: 0px;
    }
    .arrow-down {
        width: 0; 
        height: 0; 
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        
        border-top: 8px solid white;
    }
    .arrow-up {
        width: 0; 
        height: 0; 
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        
        border-bottom : 8px solid white;
    }
}
