section {
    display: flex !important;
    flex-direction: column;
    justify-content: center;
    margin-left: 25px;
    margin-right: 25px;
    margin-bottom: 50px;
}

.sectionTitle {
    font-weight: 600;
    font-size: 50px;
    color: black;
    line-height: 55px;
    text-align: left;
}

.landingContainer {
    height: 100%;
    margin: auto;
    overflow-y: scroll;
    overflow-x: hidden;
}

.titleContainer {
    margin-left: 15px;
    margin-top: 10px;
    display: flex;
    align-content: space-around;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.navBar {
    display: flex;
    flex-direction: row;
    margin-right: 10px;
}

.navItem {
    margin-right: 15px;
    font-size: 15px;
    line-height: 40px;
}

.title {
    color: black;
    font-size: 25px;
    cursor: pointer;
}

.connectButtonWrapper {
    transform: scale(0.8);
    transform-origin: top left;
}

div[aria-labelledby="rk_connect_title"] {
    top: 0px !important;
}

.smallText {
    font-size: 12px;
    margin-bottom: 0px;
    color:rgb(0, 0, 0);
}

/* Definition stlying */
.defPronun {
    color: darkgray;
    font-size: 30px;
    margin-bottom: 15px;
    margin-left: 10px;
}
.defText {
    font-size: 30px;
    color: black;
    line-height: 30px;
    width: 50vw;
    max-width: 750px;
}
.shimmer {
    background: -webkit-linear-gradient(-60deg, rgba(255,0,0,1) 0%, rgba(255,229,0,1) 25%, rgba(0,247,255,1) 50%, rgba(0,0,255,1) 75%, rgba(255,0,161,1) 100%);
    background: -moz-linear-gradient(60deg, rgba(255,0,0,1) 0%, rgba(255,229,0,1) 25%, rgba(0,247,255,1) 50%, rgba(0,0,255,1) 75%, rgba(255,0,161,1) 100%);
    background: linear-gradient(60deg, rgba(255,0,0,1) 0%, rgba(255,229,0,1) 25%, rgba(0,247,255,1) 50%, rgba(0,0,255,1) 75%, rgba(255,0,161,1) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: inline-block;
    -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
    background-repeat: no-repeat;
    animation: shimmer 2.5s infinite;
    font-weight: 600;
    font-size: 60px;
}

.greenShimmer {
    background: -webkit-linear-gradient(90.94deg, #00FF66 16.41%, #00F0FF 86.29%);
    background: -moz-linear-gradient(90.94deg, #00FF66 16.41%, #00F0FF 86.29%);
    background: linear-gradient(90.94deg, #00FF66 16.41%, #00F0FF 86.29%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: inline-block;
    -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
    background-repeat: no-repeat;
    animation: shimmer 2.5s infinite;
    font-weight: 600;
    font-size: 40px;
}

.redShimmer {
    background: -webkit-linear-gradient(90.52deg, #E7872F 0%, #E22A2A 63.57%);
    background: -moz-linear-gradient(90.52deg, #E7872F 0%, #E22A2A 63.57%);
    background: linear-gradient(90.52deg, #E7872F 0%, #E22A2A 63.57%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: inline-block;
    -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
    background-repeat: no-repeat;
    animation: shimmer 2.5s infinite;
    font-weight: 600;
    font-size: 40px;
    margin-left: 10px;
}

.blueShimmer {
    background: -webkit-linear-gradient(91.68deg, #6889FF 0%, #FF00B8 100.55%, #FF6FD7 100.55%);
    background: -moz-linear-gradient(91.68deg, #6889FF 0%, #FF00B8 100.55%, #FF6FD7 100.55%);
    background: linear-gradient(91.68deg, #6889FF 0%, #FF00B8 100.55%, #FF6FD7 100.55%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: inline-block;
    -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
    background-repeat: no-repeat;
    animation: shimmer 2.5s infinite;
    font-weight: 600;
    font-size: 40px;
    margin-left: 10px;
}
@keyframes shimmer {
    100% {-webkit-mask-position:left}
}

/* mobile */
@media only screen and (max-width: 820px) {
    .connectButtonWrapper {
        display: none;
    }
    .defText {
        font-size: 15px;
        line-height: 20px;
        width: 90vw;
    }
    .defPronun {
        color: darkgray;
        font-size: 15px;
    }
    }