
@media screen and (max-width: 600px) {
    .recording-indicator {
        top: 15px !important;
        right: 15px !important;
        width: 18px !important;
        height: 18px !important;
        border-radius: 18px !important;
    }
}

.recording-indicator {
    position: absolute;
    top: 30px;
    right: 80px;

    transform: scale(0.8);
    width: 20px;
    height: 20px;
    background-color: transparent;
    border: 2px solid white;
    border-radius: 25px;

    animation: blink-recording 1s ease-out 3s alternate infinite;
}

.recording-indicator.is-recording {
    transform: scale(1);
    border: 2px solid transparent;
    background-color: red;
    box-shadow: 0px 0px 4px 4px #fb6c6c8c;
    animation: blink-recording2 1s ease-out 4s alternate infinite;
    animation: blink-recording3 2s ease-out 3s alternate infinite;
}

@keyframes blink-recording {
    from {
        opacity: 0.7;
    }

    to {
        opacity: 1;
    }
}

@keyframes blink-recording2 {
    from {
        opacity: 0.9;
        scale: 1;
    }

    to {
        opacity: 1;
        transform: scale(1.08);
    }
}

@keyframes blink-recording3 {
    from {
        box-shadow: 0px 0px 4px 4px #fb6c6c8c;
    }

    to {
        box-shadow: 0px 0px 2px 2px #fb6c6c8c;
    }
}


