.boxOutline {
    box-sizing: border-box;
    width: 400px;
    height: 235px;
    border: 1px solid #000000;
    border-radius: 40px;
    margin-bottom: 50px;
    padding: 22px;
    display: flex;
    flex-direction: column;
    text-align: left;
}

.detailBoxOutline {
    box-sizing: border-box;
    width: 47vw;
    height: 575px;
    border: 1px solid #000000;
    border-radius: 40px;
    margin-bottom: 25px;
    padding: 22px;
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: flex-start;
}

.titleText {
    color: black;
    font-weight: 600;
    font-size: 25px;
}

.detailText {
    color: black;
    font-size: 20px;
}

@media only screen and (max-width: 820px) {
    .boxOutline {
        height: 200px;
        border-radius: 20px;
    }
    .detailBoxOutline {
        border-radius: 20px;
    }
    .titleText {
        font-size: 25px;
    }
    
    .detailText {
        font-size: 17px;
    }
    .detailBoxOutline {
        width: 90vw;
        height: fit-content;
        padding: 22px;
        display: flex;
        flex-direction: column;
        text-align: left;
    }
}