.spore-owner {
    position: absolute;
    color: #676464;
    bottom: 50px;
    left: 50%;
    right: 50%;
    transform: translateX(-50%);
    margin-left: auto;
    margin-right: auto;
    width: 280px;
    text-align: center;
    white-space: nowrap;

    .owner-name {
        margin-left: 3px;
        color: white;
    }
}

@media screen and (max-width: 600px) {
    .spore.showing-spore-player {
        .caurosel {
            .spore-player-option.chosen {
                width: 92%;
                .sp-fields {
                    margin-left: 0px;
                }
            }
        }
    }
    .recording-indicator {
        display: none;
    }
    .spore-owner {
        font-size: 8pt;
        right: 25px !important;
        top: 20px !important;
        margin-left: unset !important;
        margin-right: unset !important;
        text-align: right !important;
        width: 100px;
        white-space: unset;
        left: unset !important;
        bottom: unset !important;
        transform: unset !important;
    }
}
